@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
.Title{min-width:1660px;background-color:#3e2b70;color:#ffffff;padding:50px 0;margin-bottom:50px;display:flex;justify-content:center}.Title-Content{display:flex;align-items:center;justify-content:flex-start;width:70%}.Title-Content h2,.Title-Content p{margin:0;padding:0 10px}.Title-Content h2{font-size:36px;padding-left:0}.Title-Content p{font-size:14px}@media (max-width: 1024px){.Title{min-width:100%;padding:20px 0}.Title-Content{width:100%;flex-direction:column;justify-content:center}.Title-Content h2{font-size:30px}.Title-Content p{display:none}}

.ClubTag{display:inline-block;background-color:#3e2b70;color:white;padding:2px 8px;border-radius:10px;font-size:12px;font-weight:bold;margin:2px 0}@media (max-width: 1024px){.ClubTag{font-size:8px}}

.PersonCard{width:100%;height:320px;padding:20px;overflow:hidden;border-radius:3px;background-color:#fbfbfb}.PersonCard-ProfileWrapper{display:flex;align-items:center;justify-content:space-between}.PersonCard-ProfileWrapper-Profile{display:flex;align-items:center;justify-content:flex-start}.PersonCard-ProfileWrapper-Profile-Image{width:100px;height:100px;object-fit:cover;border-radius:50%}.PersonCard-ProfileWrapper-Profile-Name{font-size:2rem;font-weight:bold;margin-left:20px;color:#281e4d}.PersonCard-ProfileWrapper-Generation{font-size:1.25rem;color:#444}.PersonCard-Detail{display:flex;align-items:center;justify-content:space-between;margin-top:20px}.PersonCard-Detail-Club{display:flex;align-items:center;justify-content:center;flex-wrap:wrap}.PersonCard-Detail-Club>*{margin-right:5px}.PersonCard-Detail-URL{display:flex;align-items:center;justify-content:flex-start}.PersonCard-Detail-URL>*{margin-left:5px}.PersonCard-Intro{margin-top:30px;color:#281e4d;cursor:pointer}@media (max-width: 1024px){.PersonCard{width:100%;height:auto;padding:15px;overflow:hidden;border-radius:3px;background-color:#fbfbfb}.PersonCard-ProfileWrapper{display:flex;flex-direction:column;align-items:center;justify-content:space-between}.PersonCard-ProfileWrapper-Profile{display:flex;flex-direction:column;align-items:center;justify-content:flex-start}.PersonCard-ProfileWrapper-Profile-Image{width:80px;height:80px;object-fit:cover;border-radius:50%}.PersonCard-ProfileWrapper-Profile-Name{font-size:1.5rem;font-weight:bold;margin-left:20px;color:#281e4d}.PersonCard-ProfileWrapper-Generation{font-size:1rem;color:#444}.PersonCard-Detail{display:flex;flex-direction:column;align-items:center;margin-top:10px}.PersonCard-Detail-Club{display:flex;align-items:center;flex-wrap:wrap}.PersonCard-Detail-Club>*{margin-right:5px}.PersonCard-Detail-URL{display:flex;align-items:center;margin-top:20px}.PersonCard-Detail-URL>*{margin-left:5px}.PersonCard-Intro{font-size:0.75rem;margin-top:20px;color:#281e4d;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}}

.PersonList{width:1660px;margin:0 auto;margin-bottom:100px;display:grid;grid-template-columns:repeat(5, 1fr);grid-row-gap:20px;row-gap:20px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;justify-items:center}@media (max-width: 1024px){.PersonList{width:90%;grid-template-columns:repeat(2, 1fr);grid-row-gap:10px;row-gap:10px;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}}

.InfoBox .AiFillInfoCircle{cursor:pointer}@media (max-width: 1024px){.InfoBox{display:none}}

.SearchBar{width:1660px;margin:0 auto;display:flex;align-items:center;justify-content:space-between;margin-bottom:20px}.SearchBar .SearchInput{display:flex;align-items:center;justify-content:center;border:1px solid #aaa;background-color:#fbfbfb;border-radius:8px;padding:8px}.SearchBar .SearchInput input{background-color:transparent;width:200px;font-size:1.05rem;border:none;margin-left:5px}.SearchBar .SearchInput input:focus{outline:none}@media (max-width: 1024px){.SearchBar{width:90%;justify-content:center;position:fixed;left:5%;bottom:10px}.SearchBar .SearchInput{width:80%}}

*{-webkit-user-select:none;-ms-user-select:none;user-select:none;margin:0;padding:0;box-sizing:border-box;font-family:'Noto Sans KR', sans-serif}input,textarea{-webkit-user-select:auto;-ms-user-select:auto;user-select:auto}

